%fixed-top{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}


%layout{
  position: relative;
  margin: 0 auto;
  max-width: 64rem;
  width: 100%;
  min-height: 100%;
}

%cover{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

%noise{
  &:before{
    z-index:0;
    content:'';
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background-image: url(data:image/png;base64,);
    background-color:rgba(0,0,0,0.5);
  }
}

%truncate{
  position: relative;
  max-height: calc(1rem * var(--line-height-base) * var(--max-lines));
  overflow: hidden;
  padding-right: 1rem;
  &:before {
    position: absolute;
    content: "...";
    inset-block-end: 0;
    inset-inline-end: 0;
  }
  &:after {
    content: "";
    position: absolute;
    inset-inline-end: 0;
    width: 1rem;
    height: 1rem;
    background: white;
  }
}

%relative{
  position:relative;
}

%user-select{
  user-select: none;
}

%bg-white{
  background-color: #ffffff;
}


%grid-rows{
  display: grid;
  grid-auto-rows: min-content;
  grid-gap: var(--spacer);
}

%flex{
  display:flex;
}

%flex-inline{
  display:inline-flex;
}

%flex-wrap{
  flex-wrap: wrap;
}

%flex-nowrap{
  flex-wrap: nowrap;
}

%flex-row{
  @extend %flex;
  flex-direction: row;
}

%flex-row-reverse{
  @extend %flex;
  flex-direction: row-reverse;
}

%flex-column{
  @extend %flex;
  flex-direction: column;
}

%flex-column-reverse{
  @extend %flex;
  flex-direction: column-reverse;
}

%flex-middle{
  align-items: center;
}

%flex-bottom{
  align-items: flex-end;
}

%flex-top{
  align-items: flex-start;
}

%flex-start{
  justify-content: flex-start;
}

%flex-end{
  justify-content: flex-end;
}

%flex-center{
  justify-content: center;
}
